import React, { FC, useEffect, useRef, useState } from "react";
import { PolyIconProps } from "./Polygon.types";
import { Svg, Path } from "../Svg";
import { useNgon } from "./Polygon.methods";
import { rad } from "../../geometry";
import { Polygon } from "./Polygon";

export const PolyIcon: FC<PolyIconProps> = ({
	icon,
	iconScaleFactor = 0.8,
	iconPosition = [0.2, 0.1],
	...polygonProps
}) => {
	const ref = useRef(null);
	useEffect(()=>{
		const elems = (ref.current! as SVGGElement).children
		if (elems.length === 0 || elems[0].tagName !== 'svg') return; //do nothing because I wasnt trained for this
		const elem = elems[0];
		elem.removeAttribute('width');
		elem.removeAttribute('height');
	}, [icon]);
	return (<Polygon {...polygonProps} >
		<g ref={ref} transform={`scale(${iconScaleFactor}) translate(${iconPosition.join(', ')})`}>
			{icon}
		</g>
	</Polygon>);
}